import React  from "react";
import "./style.css"


interface tamakeikItem {
    price:number,
    name:string,
    description:string,
    type:string,
}

const tamakeikMenu : string[] = 
[
    "Tamales",
    "TamaKeik",
    "Lonches",
    "Capirotada",
    "Champurrado",
]

interface Props {
    selected:string;
}



class MenuBar extends React.Component {

    state:Props = {
        selected:"Tamales"
    }

    private select = (event:React.MouseEvent<HTMLButtonElement>) => {
        event.preventDefault();
        this.setState({selected:event.currentTarget.value})
    }

    render(){
        return(
            <div  className = "principal-container">

                <div className = "selected-item" >{this.state.selected}</div>

                <div className = "list-container">
                    <nav className = "list">
                            {tamakeikMenu.map((t:string,i:number)=>
                                <button 
                                    value = {t} 
                                    key = {i} 
                                    className = {this.state.selected === t?"selected-list-item":"list-item"} onClick = {this.select}
                                    >
                                    {t}
                                </button>)}
                    </nav>
                </div>

               

            </div>
        )
    }
}
export default MenuBar;